body {
  background-color:  #a785ff;
}

.App {
  background-color:  #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: rgb(27, 112,137);
  width: 200px;
  height: 50px;
  border: 0;
  padding: 0 20px;
  margin: 20px;
  font-size: 20px;
  border-radius: 10px;
  color:#fff;
}

#to-do-form input::placeholder{
  color:rgba(255,255,255, 0.5);
}

#to-do-form button{
 
  height: 50px;
  width: 80px;
  border-radius: 10px;
  border:0;
  background-color:  rgb(202, 156, 24);
  cursor:pointer;
  font-weight: bolder;
  color: white;
}
#to-do-form input:focus, #to-do-form button:focus{
  outline:none;
}